@mixin retina {
  @media (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin vp-1199 {
  @media (max-width: $vp-1199) {
    @content;
  }
}

// -   hover/focus   / 
@mixin effects-off {
  @media (min-width: $vp-1200) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: $vp-767) {
    @content;
  }
}


@mixin vp-600 {
  @media (max-width: $vp-600) {
    @content;
  }
}

@mixin vp-320 {
  @media (max-width: $vp-320) {
    @content;
  }
}

@mixin desktop-version {
  @media (min-width: $vp-1024) {
    @content;
  }
}

